import "./singleComicPage.scss"
import { useParams, Link } from 'react-router-dom';
import { useState, useEffect } from 'react';
import useMarvelService from '../services/MarvelService';
import ErrorMessage from '../errorMessage/ErrorMessage';
import Spinner from '../spinner/Spinner';
import { Helmet } from "react-helmet";

const SingleComicPage = () => {
    const {comicId} = useParams()
    const [comic, setComic] = useState(null)
    const {loading, error, clearError, getComic} = useMarvelService()

    useEffect(() => {
        updateComic()
    }, [comicId])

    const updateComic = () => {
        clearError()
        getComic(comicId)
        .then(onComicLoaded)
    }
    

    const onComicLoaded = (comic) => {
        setComic(comic)
    }

    const spinner = loading ? <Spinner/> : null
    const content = !(loading || !comic) ? <View comic={comic}/> : null 
    const errorMessage = loading || !error || comic ? null : <ErrorMessage/>

    return (
        <>
            {errorMessage}
            {spinner}
            {content}
        </>
    )
}

const View = ({comic}) => {
    const {title, description, pageCount, thumbnail, prices} = comic.data.results[0]

    return(
        
        <div className="single-comic">
            <Helmet>
                <meta 
                    name="description"
                    content={`Page of ${title} comic`}/>
                    <title>{title}</title>
            </Helmet>
            <img src={thumbnail.path + ".jpg"} alt={title} className="single-comic__img"/>
            <div className="single-comic__info">
                <h2 className="single-comic__name">{title}</h2>
                <p className="single-comic__descr">{description}</p>
                <p className="single-comic__descr">Pages count: {pageCount}</p>
                <p className="single-comic__price">Price: ${prices[0].price}</p>
            </div>
            <Link to="/comics" className="single-comic__back">Back to all</Link>
        </div>
    )
}

export default SingleComicPage;